/* src/Calculadora.css */

.calculator-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    transition: background-color 0.3s;
  }
  
  .calculator-container.light {
    background-color: #d3f4ff;
  }
  
  .calculator-container.dark {
    background-color: #2e2e2e;
  }
  
  .Calculadora {
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    width: 300px;
    text-align: center;
    transition: background-color 0.3s;
  }
  
  .calculator-container.dark .Calculadora {
    background-color: #3a3a3a;
    color: #ffffff;
  }
  
  .display {
    margin-bottom: 20px;
  }
  
  .display input {
    width: 100%;
    height: 40px;
    font-size: 20px;
    text-align: right;
    padding: 10px;
    border: none;
    border-bottom: 2px solid #ccc;
    outline: none;
    margin-bottom: 10px;
  }
  
  .display .result {
    font-size: 24px;
    font-weight: bold;
  }
  
  .buttons {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
  }
  
  .btn {
    background-color: #efefef;
    border: none;
    border-radius: 5px;
    height: 50px;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .btn:hover {
    background-color: #ddd;
  }
  
  .operator {
    background-color: #4666ff;
    color: #fff;
  }
  
  .operator:hover {
    background-color: #3657cc;
  }
  
  .decimal {
    background-color: #ff6b6b;
    color: #fff;
  }
  
  .decimal:hover {
    background-color: #cc5656;
  }
  
  .clear {
    background-color: #ff3b3b;
    color: #fff;
    grid-column: span 2;
  }
  
  .clear:hover {
    background-color: #cc2e2e;
  }
  
  .theme-toggle {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: #fff;
    border: none;
    padding: 10px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s;
  }
  
  .theme-toggle:hover {
    background-color: #ddd;
  }
  